import React from 'react';
import { Container, Button } from 'react-bootstrap';
import generatePDF from '../generatePDF'; // Adjust the path if needed

const Admissions = () => {
  return (
    <Container className="my-4 p-4 bg-light" id="admissions">
      <h1 className="text-center mb-4">Admissions</h1>
      <p className="lead text-center">We welcome applications from students who are eager to learn and grow with us. The admission process is straightforward and transparent.</p>
      
      <div className="my-4">
        <h3 className="text-primary">Admission Process</h3>
        <p>Admission forms are available for download. Submit the completed form along with the required documents at the school office.</p>
      </div>
      
      <div className="my-4">
        <h3 className="text-primary">Criteria</h3>
        <p>Admission is based on merit and the availability of seats. Entrance tests may be conducted for certain grades.</p>
      </div>
      
      <div className="my-4">
        <h3 className="text-primary">Important Dates</h3>
        <ul className="list-unstyled">
          <li><strong>Admission Form Availability:</strong> March 1st</li>
          <li><strong>Last Date for Submission:</strong> March 31st</li>
          <li><strong>Entrance Test:</strong> April 15th</li>
          <li><strong>Announcement of Results:</strong> April 30th</li>
        </ul>
      </div>
      
      <div className="text-center">
        <Button variant="primary" onClick={generatePDF}>
          Download Admission Form
        </Button>
      </div>
    </Container>
  );
};

export default Admissions;
