import React from 'react';
import { Container } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Academics = () => {
  return (
    <Container className="my-4 p-4 bg-light" id="academics">
      <h1 className="text-center mb-4">Academics</h1>
      <p className="lead text-center">At The Star Public School, we offer a diverse curriculum designed to meet the needs of our students at different levels of their education.</p>
      
      <div className="my-4">
        <h3 className="text-primary">Curriculum</h3>
        <p>Our curriculum is divided into three main stages:</p>
        <ul className="list-unstyled">
          <li><strong>Primary (Grades 1-5)</strong>: English, Mathematics, Science, Social Studies, Art, Physical Education</li>
          <li><strong>Secondary (Grades 6-10)</strong>: English, Mathematics, Science (Physics, Chemistry, Biology), Social Studies, Computer Science, Physical Education, Art</li>
          <li><strong>Senior Secondary (Grades 11-12)</strong>: 
            <ul className="list-unstyled pl-3">
              <li>Science Stream: Physics, Chemistry, Biology, Mathematics, Computer Science, English</li>
              <li>Commerce Stream: Accountancy, Business Studies, Economics, Mathematics, English</li>
            </ul>
          </li>
        </ul>
      </div>
      
      <div className="my-4">
        <h3 className="text-primary">Teaching Methodologies</h3>
        <p>We use a blend of traditional and modern teaching techniques to cater to different learning styles. Our teachers employ interactive learning modules, digital classrooms, and practical sessions to enhance the learning experience.</p>
      </div>
    </Container>
  );
};

export default Academics;
