import React from 'react';
import { Container } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Students = () => {
  return (
    <Container className="my-4" id="students">
      <h1 className="text-center mb-4">Student Life</h1>
      <p>Life at The Star Public School is vibrant and enriching, with a wide range of extracurricular activities and clubs available to students.</p>

      <h3 className="mt-4">Extracurricular Activities</h3>
      <p>We offer various activities including music, dance, drama, art, sports, robotics, and debate club. Students are encouraged to participate and explore their interests.</p>

      <h3 className="mt-4">Clubs and Societies</h3>
      <p>Our clubs include the Literary Society, Environmental Club, Astronomy Club, and Coding Club, among others.</p>

      <h3 className="mt-4">Achievements</h3>
      <p>Our students have achieved remarkable feats in academics, sports, and extracurricular activities. Some notable achievements include:</p>
      <ul>
        <li>John Smith - National Level Math Olympiad Winner</li>
        <li>Sarah Lee - Gold Medalist in State Swimming Championship</li>
        <li>Tech Innovators Club - Winners of Inter-School Robotics Competition</li>
      </ul>

      <h3 className="mt-4">Student Council</h3>
      <p>Our student council is led by:</p>
      <ul>
        <li>President: Amy Parker, Grade 12</li>
        <li>Vice President: Rajiv Mehta, Grade 11</li>
        <li>Secretary: Lisa Wong, Grade 10</li>
      </ul>
    </Container>
  );
};

export default Students;
