import React from 'react';
import { Container } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Footer = () => {
  return (
    <footer className="bg-dark text-white py-4">
      <Container>
        <p className="mb-0 text-center">&copy; 2024 The Star Public School. All rights reserved.</p>
        <p className="mb-0 text-center">123 Education Lane, Cityville, State, ZIP Code | Phone: (123) 456-7890</p>
      </Container>
    </footer>
  );
};

export default Footer;
