import React, { useState } from 'react';
import { Container, Form, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Contact = () => {
  const [formData, setFormData] = useState({ name: '', email: '', message: '' });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Handle form submission
    console.log(formData);
    // Optionally, you can clear the form after submission
    setFormData({ name: '', email: '', message: '' });
  };

  return (
    <Container className="my-4 p-4 bg-light" id="contact">
      <h1 className="text-center mb-4">Contact Us</h1>
      <p className="text-center">If you have any questions or would like more information, please feel free to contact us.</p>
      
      <Form onSubmit={handleSubmit} className="mb-4">
        <Form.Group controlId="formName" className="mb-3">
          <Form.Label>Name</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter your name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
          />
        </Form.Group>

        <Form.Group controlId="formEmail" className="mb-3">
          <Form.Label>Email address</Form.Label>
          <Form.Control
            type="email"
            placeholder="Enter your email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
          />
        </Form.Group>

        <Form.Group controlId="formMessage" className="mb-3">
          <Form.Label>Message</Form.Label>
          <Form.Control
            as="textarea"
            rows={3}
            placeholder="Your message"
            name="message"
            value={formData.message}
            onChange={handleChange}
            required
          />
        </Form.Group>

        <div className="text-center">
          <Button variant="primary" type="submit">Submit</Button>
        </div>
      </Form>

      <div className="mt-4">
        <h3 className="text-primary">Our Location</h3>
        <p>The Star Public School, 123 Education Lane, Cityville, State, ZIP Code</p>
        <p>Phone: (123) 456-7890</p>
      </div>
    </Container>
  );
};

export default Contact;
