import React from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

const Navigation = () => {
  return (
    <Navbar bg="dark" variant="dark" expand="lg" sticky="top">
      <Container>
        <Navbar.Brand as={Link} to="/">The Star Public School</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/" activeClassName="active">Home</Nav.Link>
            <Nav.Link as={Link} to="/about" activeClassName="active">About Us</Nav.Link>
            <Nav.Link as={Link} to="/academics" activeClassName="active">Academics</Nav.Link>
            <Nav.Link as={Link} to="/admissions" activeClassName="active">Admissions</Nav.Link>
            <Nav.Link as={Link} to="/faculty" activeClassName="active">Faculty</Nav.Link>
            <Nav.Link as={Link} to="/students" activeClassName="active">Students</Nav.Link>
            <Nav.Link as={Link} to="/gallery" activeClassName="active">Gallery</Nav.Link>
            <Nav.Link as={Link} to="/contact" activeClassName="active">Contact Us</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Navigation;
