import React from 'react';
import { Container, Row, Col, Image } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Gallery = () => {
  const photos = [
    { src: 'https://th.bing.com/th/id/OIP.wEwTF6KD1drzsNV9K0NuBwHaDo?rs=1&pid=ImgDetMain', alt: 'Sports Day', caption: 'Students participating in various sports events.' },
    { src: 'https://th.bing.com/th/id/OIP.Ayytk-4UGqPME8FYUK1fWQHaE8?rs=1&pid=ImgDetMain', alt: 'Science Exhibition', caption: 'Students presenting their science projects.' },
    { src: 'https://i.pinimg.com/originals/57/74/19/577419d31b2c4979e666d814a9d7de48.jpg', alt: 'Cultural Fest', caption: 'Students performing in the cultural fest.' },
    // Add more images as needed
  ];

  return (
    <Container className="my-4" id="gallery">
      <h1 className="text-center mb-4">Gallery</h1>
      <Row>
        {photos.map((photo, index) => (
          <Col md={4} key={index} className="mb-4">
            <Image src={photo.src} alt={photo.alt} fluid className="rounded mb-2" />
            <p className="text-center">{photo.caption}</p>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default Gallery;
