import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Faculty = () => {
  const facultyMembers = [
    { name: 'John Doe', position: 'Principal', qualifications: 'M.Ed', experience: '20 years' },
    { name: 'Jane Smith', position: 'Vice Principal', qualifications: 'M.Sc. in Physics', experience: '15 years' },
    { name: 'Emily Johnson', position: 'English Teacher', qualifications: 'M.A. in English', experience: '10 years' },
    // Add more faculty members as needed
  ];

  return (
    <Container className="my-4" id="faculty">
      <h1 className="text-center mb-4">Our Faculty</h1>
      <Row>
        {facultyMembers.map((member, index) => (
          <Col md={4} key={index} className="mb-4">
            <Card className="h-100 shadow-sm">
              <Card.Body>
                <Card.Title className="text-primary">{member.name}</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">{member.position}</Card.Subtitle>
                <Card.Text>
                  <strong>Qualifications:</strong> {member.qualifications}
                  <br />
                  <strong>Experience:</strong> {member.experience}
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default Faculty;
