import React from 'react';
import { Container } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const About = () => {
  return (
    <Container className="my-4" id="about">
      <h1 className="text-center">About The Star Public School</h1>
      <p className="lead text-center">Founded in 1985, The Star Public School is dedicated to providing quality education and holistic development to students.</p>
      <div className="my-4">
        <h3>Our Vision</h3>
        <p>To create a learning environment that fosters academic excellence, critical thinking, and ethical values.</p>
      </div>
      <div className="my-4">
        <h3>Our Mission</h3>
        <p>To empower students with the knowledge, skills, and values needed to thrive in a dynamic world.</p>
      </div>
      <div className="my-4">
        <h3>Message from the Principal</h3>
        <p>At The Star Public School, we believe in nurturing the potential of every student and guiding them towards a successful future.</p>
      </div>
      <div className="my-4">
        <h3>Infrastructure and Facilities</h3>
        <ul className="list-unstyled">
          <li>State-of-the-art science and computer labs</li>
          <li>Spacious and well-equipped classrooms</li>
          <li>Library with a vast collection of books and digital resources</li>
          <li>Sports facilities including a playground, gymnasium, and swimming pool</li>
        </ul>
      </div>
    </Container>
  );
};

export default About;
