import jsPDF from 'jspdf';

const generatePDF = () => {
  const doc = new jsPDF();

  // School Information
  doc.setFontSize(20);
  doc.text('The Star Public School', 20, 20);
  doc.setFontSize(12);
  doc.text('123 Education Lane, Cityville, State, ZIP Code', 20, 30);
  doc.text('Phone: (123) 456-7890 | Email: info@starpublicschool.com', 20, 40);
  doc.text('Website: www.starpublicschool.com', 20, 50);

  // Introduction
  doc.setFontSize(16);
  doc.text('Admission Form', 20, 70);
  doc.setFontSize(12);
  doc.text('Welcome! Please fill out the following details to apply for admission.', 20, 80);

  // Student Information
  doc.text('Student Information', 20, 100);
  doc.text('Full Name: _________________________________', 20, 110);
  doc.text('Date of Birth: _____________________________', 20, 120);
  doc.text('Gender: Male / Female', 20, 130);

  // Parent/Guardian Information
  doc.text('Parent/Guardian Information', 20, 150);
  doc.text('Full Name: _________________________________', 20, 160);
  doc.text('Contact Number: ___________________________', 20, 170);
  doc.text('Occupation: ________________________________', 20, 180);

  // Submission Information
  doc.text('Submission Deadline: March 31st', 20, 200);
  doc.text('Required Documents: Birth Certificate, Previous School Records', 20, 210);

  // Declaration
  doc.text('Declaration: I declare that the above information is true and correct.', 20, 230);
  doc.text('Parent/Guardian Signature: ____________________ Date: ____________', 20, 240);

  // Save the PDF
  doc.save('admission-form.pdf');
};

export default generatePDF;
